.container {
    background-color: #fff;
    border-radius: 16px;
    height: 700px;
    margin: auto;
    max-width: 390px;
    overflow: hidden;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.chat-container {
    display: flex;
    flex-direction: column;
    overflow: scroll;
    margin: 15px;
    width: 90%;
    box-shadow: 0 2px 8px 0 rgba(99, 99, 99, .2);
    border-radius: 10px;
    height: 560px;
    row-gap: 10px;

}

.chats {
    border-bottom: 2px solid #f0f0f0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;

}

.profile {
    display: flex;
}

.profile-img {
    border-radius: 50%;
    width: 80px;
    height: 80px;
    object-fit: cover;
    margin-right: 15px;

}

#search-container {
    width: 390px;
    padding: 10px;
    background: #eeeeee;
}

input {
    padding: .5em;
    width: 100%;
    font-size: 16px;
    outline: none;
    border: none;
    box-shadow: 0 2px 8px 0 rgba(99, 99, 99, .2);
    border-radius: 5px;
}


.chat-icon {
    cursor: pointer;
    margin-right: 15px;
}

.name-mail {
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.name-in-chat {
    font-weight: bold;

}

.mail-in-chat {
    font-size: 0.8rem;
    color: #878787;
    margin-top: 0;
}

.chat-icon {
    font-size: 20px;
}